import request from '@/utils/request'

// 
export const getUserPageList = (params: any) =>
  request({
    url: '/manage/users',
    method: 'get',
    params
  })

// 
export const getUserDetail = (userID: string, params: any) =>
  request({
    url: `/manage/users/${userID}`,
    method: 'get',
    params
  })

// 
export const setUserFreeze = (data: any) =>
  request({
    url: '/manage/users/freeze',
    method: 'post',
    data
  })

// 
export const setUserUnfreeze = (data: any) =>
  request({
    url: '/manage/users/unfreeze',
    method: 'post',
    data
  })
